.App {
  text-align: left;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
}
label {
  text-align: center;
}
#input {
  min-width: 10rem;
  height: 2rem;
}
